import React from "react";
import TodoItem from "./TodoItem";

const Todos = (props) => {
  return (
    <div className="container">
      <h3 className="text-center">TODOS</h3>
      {props.todo.map((todo) => {
        return (
            <TodoItem todo={todo} key={todo.sno} onDelete={props.onDelete(todo)} />
        );
      })}
    </div>
  );
};

export default Todos
