import React from "react";

const Todo = (props) => {
  return (
    <div>
      <h4>{props.todo.title}</h4>
      <p>{props.todo.desc}</p>
      <p>{props.todo.sno}</p>
      <button className="btn-danger btn-sm" onClick={props.onDelete}>Delete</button>
    </div>
  );
};

export default Todo;
